import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import Select from "react-select";

export default function Add() {
  const MySwal = withReactContent(Swal)

  const [data, setData] = useState({
    branch_name : "",
    license : "",
    cashier : "",
    name : "",
    email : "",
    password_confirmation : "",
    password : "",
    licenseOptions : [
      {value : 1 , label : "Versi Trial 30 hari",status : 'license'},
      {value : 2 , label : "Versi Premium",status : 'license'},
      {value : 3 , label : "Versi Multi Cabang",status : 'license'}
    ],
    cashierOptions : [
      {value : 1 , label : "Transaksi",status : 'cashier'},
      {value : 0 , label : "Non Transaksi",status : 'cashier'},
    ]
  })

  function submit(e) {
    e.preventDefault()
    // MySwal.fire({
    //   title: "Yakin akan menambahkan data?",
    //   icon: "info",
    //   showCloseButton: true,
    //   showCancelButton: true,
    //   focusConfirm: false,
    //   confirmButtonText: 'Yakin',
    //   cancelButtonText: 'Batal'

    // }).then((val) => {
      Axios.post(process.env.REACT_APP_URL_BACKEND + "/api/admin/user", {
        branch_name : data.branch_name,
        license : data.license,
        cashier : data.cashier,
        name : data.name,
        email : data.email,
        password_confirmation : data.password_confirmation,
        password : data.password
      }, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      }).then(res => {
        MySwal.fire({
          title: 'Berhasil',
          text: 'Data berhasil ditambahkan',
          icon: 'success'
        });

        window.location.href = process.env.REACT_APP_URL_FRONTEND + "/admin/user-utama"
      }).catch((err) => {
        MySwal.fire({
          title: 'Gagal',
          text: err.response.data.message,
          icon: 'error'
        });
      });
    // })
  }

  function handle(e) {
    const newData = { ...data }
    if(e.status == 'license'){
      newData['license'] = e.value
    }else if(e.status == 'cashier'){
      newData['cashier'] = e.value
    }else{
      newData[e.target.id] = e.target.value
    }
    setData(newData)
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white">
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <Link
                    className="bg-black text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/admin/user-utama"
                  >
                    ← Kembali
                  </Link>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <h3 className="font-semibold text-lg text-dark">
                    Tambah Cabang
                  </h3>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">

              <form onSubmit={(e) => submit(e)} className="p-5">
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama"
                    onChange={(e) => handle(e)}
                    id="name"
                    value={data.name}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama Cabang
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama Cabang"
                    onChange={(e) => handle(e)}
                    id="branch_name"
                    value={data.branch_name}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Lisensi
                  </label>
                  <Select
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    onChange={(e) => handle(e)}
                    id="license"
                    options={data.licenseOptions}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Email"
                    onChange={(e) => handle(e)}
                    id="email"
                    value={data.email}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Jenis
                  </label>
                  <Select
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    onChange={(e) => handle(e)}
                    id="cashier"
                    options={data.cashierOptions}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Password
                  </label>
                  <input
                    type="password"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Password"
                    onChange={(e) => handle(e)}
                    id="password"
                    value={data.password}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Konfirmasi Password 
                  </label>
                  <input
                    type="password"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Konfirmasi Password"
                    onChange={(e) => handle(e)}
                    id="password_confirmation"
                    value={data.password_confirmation}
                  />
                </div>

                <div className="text-center mt-6">
                  <button
                    className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                    type="submit"
                  >
                    Simpan
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
