import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

// components

import Navbar from "components/Navbars/Navbar.js";
import SidebarUserCabang from "components/Sidebar/SidebarUserCabang.js";
import HeaderStats from "components/Headers/HeaderStats.js";
import Footer from "components/Footers/Footer.js";

// views

import Dashboard from "views/userCabang/Dashboard.js";
import Report from "views/userCabang/Report.js";
import StockIn from "views/userCabang/StockIn/Index.js";
import StockInAdd from "views/userCabang/StockIn/Add.js";
import StockOut from "views/userCabang/StockOut/Index.js";
import StockOutAdd from "views/userCabang/StockOut/Add.js";
import Profile from "views/userCabang/Profile.js";
import Axios from "axios";

export default function UserCabang() {
  const [data, setData] = useState({
    loadedData: 0
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "loadedData": 1
      })
      
      if(res.data.data.level.id != 3){
        if(res.data.data.level.id == 2){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama"
        }
        if(res.data.data.level.id == 1){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/admin"
        }
      }
    }).catch((err) => {
      window.location.href = process.env.REACT_APP_URL_FRONTEND + "/auth/login"
    });
  }
  
  return (
    <>
      <SidebarUserCabang />
      <div className="relative md:ml-64 bg-blueGray-100">
        <Navbar />
        {/* Header */}
        <HeaderStats />
        <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <Switch>
            <Route path="/user-cabang/dashboard" exact component={Dashboard} />
            <Route path="/user-cabang/report" exact component={Report} />
            <Route path="/user-cabang/stock-in" exact component={StockIn} />
            <Route path="/user-cabang/stock-in/add" exact component={StockInAdd} />
            <Route path="/user-cabang/stock-out" exact component={StockOut} />
            <Route path="/user-cabang/stock-out/add" exact component={StockOutAdd} />
            <Route path="/user-cabang/profile" exact component={Profile} />
            <Redirect from="/user-cabang" to="/user-cabang/dashboard" />
          </Switch>
          {/* <Footer /> */}
        </div>
      </div>
    </>
  );
}
