import Axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

export default function Dashboard() {

    const [data, setData] = useState({
        loadedData: 0,
        primary_user_count: 0,
        sku_count: 0,
        branch_count: 0,
        dataMain: [],
    })

    useEffect(() => {
        if (data.loadedData == 0) {
            getData()
        }
    })

    async function getData() {
        let newData = [];
        await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/admin/dashboard", {
            headers: {
                Accept: "application/json",
                Authorization: `Bearer ${localStorage.getItem('token')}`
            }
        }).then(res => {
            newData['branch_name'] = res.data.data.branch_name
            newData['primary_user_count'] = res.data.data.primary_user_count
            newData['sku_count'] = res.data.data.sku_count
            newData['branch_count'] = res.data.data.branch_count
            newData['loadedData'] = 1
        })
        
        await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/admin/user", {
            headers: {
                Accept: "application/json",
                Authorization: `Bearer ${localStorage.getItem('token')}`
            }
        }).then(res => {
            newData['dataMain'] = res.data.data
            newData['loadedData'] = 1
        })
        setData(newData)
    }

    return (
        <>
            <div className="flex flex-wrap">

                <div className="w-full xl:w-8/12 mb-12 xl:mb-0 px-4">
                    <div className="relative flex flex-col min-w-0 break-words bg-transparent w-full mb-6 rounded">
                        <div className="rounded-t mb-0 px-4 py-3 bg-transparent">
                            <div className="flex flex-wrap items-center">
                                <div className="relative w-full max-w-full flex-grow flex-1 text-white mb-3">
                                    <h1 className="text-5xl font-semibold">Hallo, TIM!👋</h1>
                                    <p>Ini adalah halaman semua informasi yang Anda butuhkan</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                    <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                        <div className="rounded-t mb-0 px-4 py-3 bg-white">
                            <div className="flex flex-wrap items-center">
                                <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                    <h3 className="text-1xl font-semibold">Jumlah SKU</h3>
                                    <p className="text-2xl font-semibold">{data.sku_count}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                    <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                        <div className="rounded-t mb-0 px-4 py-3 bg-white">
                            <div className="flex flex-wrap items-center">
                                <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                    <h3 className="text-1xl font-semibold">Jumlah Cabang</h3>
                                    <p className="text-2xl font-semibold">{data.branch_count}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded">
                    <div className="rounded-t mb-0 px-4 py-3 border-0">
                        <div className="flex flex-wrap items-center">
                            <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                                <div className="font-semibold text-base text-blueGray-700">
                                    <p className="mb-3 text-gray">Total User Utama</p>
                                    <h1 className="text-2xl">{data.primary_user_count}</h1>
                                </div>
                            </div>
                            <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                                <Link
                                    className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                                    type="button"
                                    to="/admin/user-utama"
                                >
                                    Lihat Semua Cabang
                                </Link>
                            </div>
                        </div>
                    </div>
                    <div className="block w-full overflow-x-auto mt-2">
                        {/* Projects table */}
                        <table className="items-center w-full bg-transparent border-collapse">
                            <thead>
                                <tr>
                                    <th className="px-6 bg-black text-white align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                                        No
                                    </th>
                                    <th className="px-6 bg-black text-white align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                                        Nama
                                    </th>
                                    <th className="px-6 bg-black text-white align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                                        Nama Cabang
                                    </th>
                                    <th className="px-6 bg-black text-white align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                                        Email
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    data.dataMain?.map((val, i) => {
                                        return <tr key={i}>
                                            <th className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-left">
                                                {i + 1}
                                            </th>
                                            <th className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-left">
                                                {val.name}
                                            </th>
                                            <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                                                {val.branch_name}
                                            </td>
                                            <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                                                {val.email}
                                            </td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    );
}
