import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import DataTable from "react-data-table-component";
// components

export default function Index() {
  const MySwal = withReactContent(Swal)

  const [main, setMain] = useState([]);
  const [loading, setLoading] = useState(false);
  const [from, setFrom] = useState(false);
  const [totalRows, setTotalRows] = useState(0);
  const [perPage, setPerPage] = useState(10);

  const fetchMains = async page => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/admin/user", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setTotalRows(response.data.total);
    setFrom(response.data.from);
    setLoading(false);
  };

  const handlePageChange = page => {
    fetchMains(page);
  };

  const handlePerRowsChange = async (newPerPage, page) => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/admin/user", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setPerPage(newPerPage);
    setLoading(false);
  };

  useEffect(() => {
    fetchMains(1);
  }, []);

  const columns = [
    {
      name: '#',
      cell: (row, index) => index + from
    },
    {
      name: 'Name',
      selector: 'name',
    },
    {
      name: 'Name',
      selector: 'branch_name',
    },
    {
      name: 'Email',
      selector: 'email',
    },
    {
      name: 'Opsi',
      width: "20%",
      cell: row => {
        return <>
          <Link
            className="bg-yellow-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
            type="button"
            to={"/admin/user-utama/edit/" + row.id}
          >
             <i className="fas fa-pen text-sm "></i>
          </Link>
          <button
            className="bg-red-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
            type="button"
            data-id={row.id}
            onClick={(e) => doDelete(e)}
          >
             <i className="fas fa-trash text-sm "></i>
          </button>
        </>
      },
    }
  ];

  function doDelete(e) {
    e.preventDefault()
    const userId = e.target.getAttribute("data-id")

    MySwal.fire({
      title: "Yakin akan menghapus data?",
      icon: "info",
      showCloseButton: true,
      showCancelButton: true,
      focusConfirm: false,
      confirmButtonText: 'Yakin',
      cancelButtonText: 'Batal'

    }).then((val) => {
      if (val.isConfirmed === true) {
        Axios.delete(process.env.REACT_APP_URL_BACKEND + "/api/admin/user/" + userId, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${localStorage.getItem('token')}`
          }
        }).then(res => {
          fetchMains(1)
          MySwal.fire({
            title: 'Berhasil',
            text: 'Data berhasil di hapus',
            icon: 'success'
          });

        })
      }
    })
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">

          <div
            className={
              "relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white"
            }
          >
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <h3
                    className={
                      "font-semibold text-lg text-dark"
                    }
                  >
                    Daftar Cabang
                  </h3>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <Link
                    className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/admin/user-utama/add"
                  >
                    Tambah Cabang
                  </Link>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">
              <DataTable
                columns={columns}
                data={main}
                progressPending={loading}
                pagination
                paginationServer
                paginationTotalRows={totalRows}
                onChangeRowsPerPage={handlePerRowsChange}
                onChangePage={handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
