import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import Select from "react-select";

export default function Add() {
  const MySwal = withReactContent(Swal)

  const [data, setData] = useState({
    loadedData: 0,
    supplier : "",
    image : [],
    sku : "",
    name : "",
    category : "",
    product_unit : "",
    purchase_price : "",
    selling_price : "",
    discount_price_type : "",
    discount_price : "",
    stock_type : "",
    productUnitOptions : [],
    categoryOptions : [],  
    discountTypeOptions : []
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  async function getData() {
    let categoryAll = []
    let unitAll = []

    await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/category", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      if(res.data.data)
      res.data.data.map((val,i) => {
        categoryAll.push({
          value : val.id,
          label : val.name,
          status : 'category'
        })
      })
    })

    await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/product-unit", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      if(res.data.data)
      res.data.data.map((val,i) => {
        unitAll.push({
          value : val.id,
          label : val.name,
          status : 'product_unit'
        })
      })
    })
    setData({
      "categoryOptions" : categoryAll,  
      "productUnitOptions" : unitAll,  
      "discountTypeOptions" : [
        {value : 0, label : "Diskon Persen", status : 'discountType'},
        {value : 1, label : "Diskon Harga", status : 'discountType'},
      ],  
      "loadedData": 1
    })
  }

  function submit(e) {
    e.preventDefault()

    // MySwal.fire({
    //   title: "Yakin akan menambahkan data?",
    //   icon: "info",
    //   showCloseButton: true,
    //   showCancelButton: true,
    //   focusConfirm: false,
    //   confirmButtonText: 'Yakin',
    //   cancelButtonText: 'Batal'

    // }).then((val) => {
      const formData = new FormData();
      formData.append("supplier", data.supplier)
      formData.append("image", data.image)
      formData.append("sku", data.sku)
      formData.append("name", data.name)
      formData.append("category", data.category)
      formData.append("product_unit", data.product_unit)
      formData.append("purchase_price", data.purchase_price)
      formData.append("selling_price", data.selling_price)
      formData.append("discount_price_type", data.discount_price_type)
      formData.append("discount_price", data.discount_price)
      formData.append("stock", data.stock)
      formData.append("stock", data.stock_type)

      Axios.post(process.env.REACT_APP_URL_BACKEND + "/api/branch/product", formData, {
        headers: {
          Accept: "application/json",
          'content-type': 'multipart/form-data',
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      }).then(res => {
        MySwal.fire({
          title: 'Berhasil',
          text: 'Data berhasil ditambahkan',
          icon: 'success'
        });
        window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama/product"
      }).catch((err) => {
        MySwal.fire({
          title: 'Gagal',
          text: err.response.data.message,
          icon: 'error'
        });
      });
    // })
  }

  function handle(e) {
    const newData = { ...data }
    if(e.status == 'category'){
      newData['category'] = e.value
    }else if(e.status == 'product_unit'){
      newData['product_unit'] = e.value
    }else if(e.status == 'discountType'){
      newData['discount_price_type'] = e.value
    }else{
      newData[e.target.id] = e.target.id !== "image" ? e.target.value : e.target.files[0]
    }
    setData(newData)
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white">
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <Link
                    className="bg-black text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/user-utama/product"
                  >
                    ← Kembali
                  </Link>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <h3 className="font-semibold text-lg text-dark">
                    Tambah Produk
                  </h3>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">

              <form onSubmit={(e) => submit(e)} className="p-5">
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Gambar
                  </label>
                  <input
                    type="file"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Gambar"
                    onChange={(e) => handle(e)}
                    id="image"
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    SKU
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="SKU"
                    onChange={(e) => handle(e)}
                    id="sku"
                    value={data.sku}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama"
                    onChange={(e) => handle(e)}
                    id="name"
                    value={data.name}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Kategori
                  </label>
                  <Select
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Kategori"
                    onChange={(e) => handle(e)}
                    id="category"
                    options={data.categoryOptions}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Unit
                  </label>
                  <Select
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Unit"
                    onChange={(e) => handle(e)}
                    id="product_unit"
                    options={data.productUnitOptions}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Harga Beli
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Harga Beli"
                    onChange={(e) => handle(e)}
                    id="purchase_price"
                    value={data.purchase_price}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Harga Jual
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Harga Jual"
                    onChange={(e) => handle(e)}
                    id="selling_price"
                    value={data.selling_price}
                  />
                </div>
                
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Jenis Diskon
                  </label>
                  <Select
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Jenis Diskon"
                    onChange={(e) => handle(e)}
                    id="discount_price_type"
                    options={data.discountTypeOptions}
                  />
                </div>
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    {
                      data.discount_price_type == 0 ? "Masukkan Persen Diskon" : "Masukkan Harga Diskon"
                    }
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Harga Diskon"
                    onChange={(e) => handle(e)}
                    id="discount_price"
                    value={data.discount_price}
                  />
                </div>
                

                <div className="text-center mt-6">
                  <button
                    className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                    type="submit"
                  >
                    Simpan
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
