import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Axios from "axios";

// components

import Navbar from "components/Navbars/Navbar.js";
import SidebarUserUtama from "components/Sidebar/SidebarUserUtama.js";
import HeaderStats from "components/Headers/HeaderStats.js";
import Footer from "components/Footers/Footer.js";

// views

import Dashboard from "views/userUtama/Dashboard.js";
import Supplier from "views/userUtama/Supplier/Index.js";
import SupplierDetail from "views/userUtama/Supplier/Detail.js";
import SupplierAdd from "views/userUtama/Supplier/Add.js";
import SupplierEdit from "views/userUtama/Supplier/Edit.js";
import Customer from "views/userUtama/Customer/Index.js";
import CustomerDetail from "views/userUtama/Customer/Detail.js";
import CustomerAdd from "views/userUtama/Customer/Add.js";
import CustomerEdit from "views/userUtama/Customer/Edit.js";
import Product from "views/userUtama/Product/Index.js";
import ProductDetail from "views/userUtama/Product/Detail.js";
import ProductAdd from "views/userUtama/Product/Add.js";
import ProductEdit from "views/userUtama/Product/Edit.js";
import StockIn from "views/userUtama/StockIn/Index.js";
import StockInAdd from "views/userUtama/StockIn/Add.js";
import StockOut from "views/userUtama/StockOut/Index.js";
import StockOutAdd from "views/userUtama/StockOut/Add.js";
import Transaction from "views/userUtama/Transaction/Index.js";
import TransactionAdd from "views/userUtama/Transaction/Add.js";
import Report from "views/userUtama/Report.js";
import Setting from "views/userUtama/Setting.js";
import UserCabang from "views/userUtama/UserCabang/Index.js";
import UserCabangDetail from "views/userUtama/UserCabang/Detail.js";
import UserCabangAdd from "views/userUtama/UserCabang/Add.js";
import UserCabangEdit from "views/userUtama/UserCabang/Edit.js";
import Profile from "views/userUtama/Profile.js";

export default function UserUtama() {
  const [data, setData] = useState({
    loadedData: 0,
    cashier : 0,
    license : 1
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "loadedData": 1,
        "cashier" : res.data.data.cashier,
        "license" : res.data.data.branch.license.id
      })
      
      if(res.data.data.level.id != 2){
        if(res.data.data.level.id == 1){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/admin"
        }
        if(res.data.data.level.id == 3){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-cabang"
        }
      }
    }).catch((err) => {
      window.location.href = process.env.REACT_APP_URL_FRONTEND + "/auth/login"
    });
  }
  
  return (
    <>
      <SidebarUserUtama cashier={data.cashier} license={data.license} />
      <div className="relative md:ml-64 bg-blueGray-100">
        <Navbar license={data.license}  />
        {/* Header */}
        <HeaderStats />
        <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <Switch>
            <Route path="/user-utama/dashboard" exact render={(props) => <Dashboard {...props} cashier={data.cashier} />} />
            <Route path="/user-utama/supplier" exact component={Supplier} />
            <Route path="/user-utama/supplier/detail/:id" exact component={SupplierDetail} />
            <Route path="/user-utama/supplier/add" exact component={SupplierAdd} />
            <Route path="/user-utama/supplier/edit/:id" exact component={SupplierEdit} />
            <Route path="/user-utama/customer" exact component={Customer} />
            <Route path="/user-utama/customer/detail/:id" exact component={CustomerDetail} />
            <Route path="/user-utama/customer/add" exact component={CustomerAdd} />
            <Route path="/user-utama/customer/edit/:id" exact component={CustomerEdit} />
            <Route path="/user-utama/product" exact component={Product} />
            <Route path="/user-utama/product/detail/:id" exact component={ProductDetail} />
            <Route path="/user-utama/product/add" exact component={ProductAdd} />
            <Route path="/user-utama/product/edit/:id" exact component={ProductEdit} />
            <Route path="/user-utama/stock-in" exact component={StockIn} />
            <Route path="/user-utama/stock-in/add" exact component={StockInAdd} />
            <Route path="/user-utama/stock-out" exact component={StockOut} />
            <Route path="/user-utama/stock-out/add" exact component={StockOutAdd} />
            <Route path="/user-utama/transaction" exact component={Transaction} />
            <Route path="/user-utama/transaction/add" exact component={TransactionAdd} />
            <Route path="/user-utama/report" exact component={Report} />
            <Route path="/user-utama/setting" exact component={Setting} />
            <Route path="/user-utama/user-cabang" exact component={UserCabang} />
            <Route path="/user-utama/user-cabang/detail/:id" exact component={UserCabangDetail} />
            <Route path="/user-utama/user-cabang/add" exact component={UserCabangAdd} />
            <Route path="/user-utama/user-cabang/edit/:id" exact component={UserCabangEdit} />
            <Route path="/user-utama/profile" exact component={Profile} />
            <Redirect from="/user-utama" to="/user-utama/dashboard" />
          </Switch>
          {/* <Footer /> */}
          {/* <div id="tooltip-detail" role="tooltip" class="inline-block absolute invisible z-10 py-2 px-3 text-sm font-medium text-white bg-gray-900 rounded-lg shadow-sm opacity-0 transition-opacity duration-300 tooltip dark:bg-gray-700">
              Detail
              <div class="tooltip-arrow" data-popper-arrow></div>
          </div> */}
        </div>
      </div>
    </>
  );
}
