import Axios from "axios";
import React, { useState } from "react";

export default function Report(props) {

  const [data, setData] = useState({
    dateFrom: "",
    dateTo: "",
    month: 0
  })

  function downloadReport(e) {
    e.preventDefault()
    const type = e.target.id.split("-")[0]
    const report_type = e.target.id.split("-")[1]

    Axios.post(process.env.REACT_APP_URL_BACKEND + "/api/branch/report/export", {
      start_date: data.dateFrom,
      end_date: data.dateTo,
      type: type,
      month: data.month,
      report_type: report_type,
      export_type: 'excel',
    }, {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      },
    }).then(res => {
      window.location.href = res.data.data.url
    })
  }

  function handle(e) {
    const newData = { ...data }
    newData[e.target.id] = e.target.value
    setData(newData)
  }

  return (
    <>
      <div className="flex flex-wrap">

        <div className="w-full xl:w-8/12 mb-12 xl:mb-0 px-4">
          <div className="relative flex flex-col min-w-0 break-words bg-transparent w-full mb-6 rounded">
            <div className="rounded-t mb-0 px-4 py-3 bg-transparent">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full max-w-full flex-grow flex-1 text-white mb-3">
                  <h1 className="text-5xl font-semibold">Laporan</h1>
                </div>
              </div>
            </div>
          </div>
        </div>
        {
          props.cashier == 1 ?
            <>
              <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                  <div className="rounded-t mb-0 px-4 py-3 bg-white">
                    <div className="flex flex-wrap items-center">
                      <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                        <h3 className="text-1xl font-semibold mb-4">Laporan Transaksi Bulanan</h3>
                        <input
                          type="month"
                          className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                          onChange={(e) => handle(e)}
                          id="month"
                          value={data.month}
                        />
                        <a id="transaction-monthly" onClick={(e) => downloadReport(e)} className="bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">
                          Download
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                  <div className="rounded-t mb-0 px-4 py-3 bg-white">
                    <div className="flex flex-wrap items-center">
                      <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                        <h3 className="text-1xl font-semibold mb-4">Laporan Transaksi Harian</h3>
                        <div className="flex flex-wrap">
                          <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                            <p>Dari Tanggal</p>
                            <input
                              type="date"
                              className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                              onChange={(e) => handle(e)}
                              id="dateFrom"
                              value={data.dateFrom}
                            />
                          </div>
                          <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                            <p>Sampai Tanggal</p>
                            <input
                              type="date"
                              className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                              onChange={(e) => handle(e)}
                              id="dateTo"
                              value={data.dateTo}
                            />
                          </div>
                        </div>
                        <a id="transaction-daily" onClick={(e) => downloadReport(e)} className="ml-3 bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">
                          Download
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
            :
            <>
              <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                  <div className="rounded-t mb-0 px-4 py-3 bg-white">
                    <div className="flex flex-wrap items-center">
                      <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                        <h3 className="text-1xl font-semibold mb-4">Laporan Stok Bulanan</h3>
                        <input
                          type="month"
                          className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                          onChange={(e) => handle(e)}
                          id="month"
                          value={data.month}
                        />
                        <a id="stock-monthly" onClick={(e) => downloadReport(e)} className="bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">
                          Download
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                  <div className="rounded-t mb-0 px-4 py-3 bg-white">
                    <div className="flex flex-wrap items-center">
                      <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                        <h3 className="text-1xl font-semibold mb-4">Laporan Stok Harian</h3>
                        <div className="flex flex-wrap">
                          <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                            <p>Dari Tanggal</p>
                            <input
                              type="date"
                              className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                              onChange={(e) => handle(e)}
                              id="dateFrom"
                              value={data.dateFrom}
                            />
                          </div>
                          <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                            <p>Sampai Tanggal</p>
                            <input
                              type="date"
                              className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                              onChange={(e) => handle(e)}
                              id="dateTo"
                              value={data.dateTo}
                            />
                          </div>
                        </div>
                        <a id="stock-daily" onClick={(e) => downloadReport(e)} className="ml-3 bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">
                          Download
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
        }



      </div>
    </>
  );
}
