import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import Select from "react-select";

export default function Add() {
  const MySwal = withReactContent(Swal)

  const [products, setProducts] = useState([
    {
      id: 0,
      price: 0,
      qty: 0,
    }
  ])

  const [data, setData] = useState({
    allStockTypeOptions: [],
    allProductOptions: [],
    loadedData: 0,
    totalPrice: 0,

    payment_type: "cash",
    paid_total: "",
    stock_type: 1,
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  async function getData() {
    let allProduct = []

    await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/product/all", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      if (res.data.data)
        res.data.data.map((val, i) => {
          allProduct.push({
            value: val.id,
            label: val.name,
            price: val.price,
            status: 'product'
          })
        })
    })

    setData({
      "allProductOptions": allProduct,
      "loadedData": 1,
    })
  }

  function submit(e) {
    e.preventDefault()
    console.log(products)

    // MySwal.fire({
    //   title: "Yakin akan menambahkan data?",
    //   icon: "info",
    //   showCloseButton: true,
    //   showCancelButton: true,
    //   focusConfirm: false,
    //   confirmButtonText: 'Yakin',
    //   cancelButtonText: 'Batal'

    // }).then((val) => {
      let products_data = []

      products.map((val_data, i) => {
        products_data.push({
          id: val_data.id,
          qty: val_data.qty
        })
      })
      
      let formData = {
        "products" : products_data,
        "paid_total" : data.paid_total,
        "payment_type" : 'cash'
      }

      // formData.append("products", JSON.parse(JSON.stringify(products_data)))
      // formData.append("paid_total", data.paid_total)
      // formData.append("payment_type", data.payment_type)

      Axios.post(process.env.REACT_APP_URL_BACKEND + "/api/branch/transaction", formData, {
        headers: {
          Accept: "application/json",
          // 'content-type': 'json',
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      }).then(res => {
        MySwal.fire({
          title: 'Berhasil',
          text: 'Data berhasil ditambahkan',
          icon: 'success'
        });
        window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama/transaction"
      }).catch((err) => {
        MySwal.fire({
          title: 'Gagal',
          text: err.response.data.message,
          icon: 'error'
        });
      });
    // })
  }

  function addProduct() {
    setProducts([...products, {
      id: 0,
      price: 0,
      qty: 0,
    }]);
  }

  function remove(id) {
    const id_remove = products[id].id
    const qty_remove = products[id].qty
    let products_data = []

    Object.keys(products).forEach(function(key) {
      if(products[key].id != id_remove){
        products_data.push(products[key])
      }
    });
    setProducts(products_data)
  }

  function handle(e) {
    const newData = { ...data }
    const newProducts = { ...products }

    if (e.status == 'stock_type') {
      newData['stock_type'] = e.value
    } else if (e.status == 'product') {
      newProducts[e.title].id = e.value
      newProducts[e.title].price = e.price
    } else if (e.target.id == 'product_qty') {
      newProducts[e.target.title].qty = e.target.value
    } else {
      newData[e.target.id] = e.target.value
    }

    if (e.status == 'product' || e.target.id == 'product_qty') {
      let products_data = []
      Object.keys(newProducts).forEach(function(key) {
        products_data.push(newProducts[key])
      });
      setProducts(products_data)
    } else {
      setData(newData)
    }
  }


  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white">
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <Link
                    className="bg-black text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/user-utama/stock-in"
                  >
                    ← Kembali
                  </Link>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <h3 className="font-semibold text-lg text-dark">
                    Tambah Transaksi
                  </h3>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">

              <form onSubmit={(e) => submit(e)} className="p-5">

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Produk
                  </label>
                  <button type="button" onClick={() => addProduct()} className="bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">Tambah</button>
                  {
                    products.map((val, i) => {
                      return <div style={{display: 'flex',marginTop:'10px'}}>
                        <div style={{width:"300px"}}>
                        <Select
                          title={i}
                          menuPlacement="auto"
                          className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none ease-linear transition-all duration-150"
                          placeholder="Pilih Produk"
                          onChange={(e) => handle(e)}
                          id="product_id"
                          options={data.allProductOptions.map((val_op, i_op)=>{
                            return {
                              value: val_op.value,
                              label: val_op.label,
                              price: val_op.price,
                              status: 'product',
                              title: i
                            }
                          })}
                        />
                        </div>
                        <input
                          title={i}
                          style={{margin:"0 10px"}}
                          type="number"
                          className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none ease-linear transition-all duration-150"
                          placeholder="Kuantitas"
                          onChange={(e) => handle(e)}
                          id="product_qty"
                          value={val.qty}
                        />
                        <button type="button" onClick={() => remove(i)} className="bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">Hapus</button>
                      </div>
                    })
                  }
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Total Bayar ({
                      () => {
                        let totalPrice = 0;
                        products.map((val,i) => {
                          totalPrice += val.price * val.qty
                        })
                        return totalPrice
                      }
                    })
                  </label>
                  <input
                    type="number"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Total Bayar"
                    onChange={(e) => handle(e)}
                    id="paid_total"
                    value={data.paid_total}
                  />
                </div>

                <div className="text-center mt-6">
                  <button
                    className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                    type="submit"
                  >
                    Simpan
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
