import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';

export default function Detail() {

  const [data, setData] = useState({
    loadedData: 0,
    dataMain: []
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    const id = window.location.href.split("/")[3]
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/manage" + id, {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "dataMain": {
          branch_name: res.data.data.branch_name,
          license: res.data.data.license,
          cashier: res.data.data.cashier,
          name: res.data.data.name,
          email: res.data.data.email,
        }
      })
    })
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white">
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <Link
                    className="bg-black text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/user-utama/setting"
                  >
                    ← Kembali
                  </Link>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">

              <form className="p-5">
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama"
                    disabled
                    value={data.dataMain.name}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama Cabang
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama Cabang"
                    disabled
                    value={data.dataMain.branch_name}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Lisensi
                  </label>
                  <select
                    className="border-0 placehold
                    disableder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    value={data.dataMain.license}
                  >
                    <option value="1">Versi Trial 30 hari</option>
                    <option value="2">Versi Premium</option>
                    <option value="3">Versi Multi Cabang</option>
                  </select>
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Email"
                    disabled
                    value={data.dataMain.email}
                  />
                </div>

                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Penjualan?
                  </label>
                  <select
                    className="border-0 placehold
                    disableder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    value={data.dataMain.cashier}
                  >
                    <option value="1">Ya</option>
                    <option value="0">Tidak</option>
                  </select>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
