import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Axios from "axios";

// components

import Navbar from "components/Navbars/Navbar.js";
import SidebarAdmin from "components/Sidebar/SidebarAdmin.js";
import HeaderStats from "components/Headers/HeaderStats.js";

// views

import Dashboard from "views/admin/Dashboard.js";
import UserUtama from "views/admin/UserUtama/Index.js";
import UserUtamaAdd from "views/admin/UserUtama/Add.js";
import UserUtamaEdit from "views/admin/UserUtama/Edit.js";
import Category from "views/admin/Category/Index.js";
import CategoryAdd from "views/admin/Category/Add.js";
import CategoryEdit from "views/admin/Category/Edit.js";
import Unit from "views/admin/Unit/Index.js";
import UnitAdd from "views/admin/Unit/Add.js";
import UnitEdit from "views/admin/Unit/Edit.js";
import Stock from "views/admin/Stock/Index.js";
import StockAdd from "views/admin/Stock/Add.js";
import StockEdit from "views/admin/Stock/Edit.js";


export default function Admin() {
  const [data, setData] = useState({
    loadedData: 0
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "loadedData": 1
      })
      
      if(res.data.data.level.id != 1){
        if(res.data.data.level.id == 2){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama"
        }
        if(res.data.data.level.id == 3){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-cabang"
        }
      }
    }).catch((err) => {
      window.location.href = process.env.REACT_APP_URL_FRONTEND + "/auth/login"
    });
  }
  
  return (
    <>
      <SidebarAdmin />
      <div className="relative md:ml-64 bg-blueGray-100">
        <Navbar />
        {/* Header */}
        <HeaderStats />
        <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <Switch>
            <Route path="/admin/dashboard" exact component={Dashboard} />
            <Route path="/admin/user-utama" exact component={UserUtama} />
            <Route path="/admin/user-utama/add" exact component={UserUtamaAdd} />
            <Route path="/admin/user-utama/edit/:id" exact component={UserUtamaEdit} />
            <Route path="/admin/category" exact component={Category} />
            <Route path="/admin/category/add" exact component={CategoryAdd} />
            <Route path="/admin/category/edit/:id" exact component={CategoryEdit} />
            <Route path="/admin/unit" exact component={Unit} />
            <Route path="/admin/unit/add" exact component={UnitAdd} />
            <Route path="/admin/unit/edit/:id" exact component={UnitEdit} />
            <Route path="/admin/stock-type" exact component={Stock} />
            <Route path="/admin/stock-type/add" exact component={StockAdd} />
            <Route path="/admin/stock-type/edit/:id" exact component={StockEdit} />
            <Redirect from="/admin" to="/admin/dashboard" />
          </Switch>
          {/* <FooterAdmin /> */}
        </div>
      </div>
    </>
  );
}
