import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import DataTable from "react-data-table-component";
// components

export default function Index() {

  const [data, setData] = useState({
    month: 0
  })
  const [main, setMain] = useState([]);
  const [loading, setLoading] = useState(false);
  const [from, setFrom] = useState(false);
  const [totalRows, setTotalRows] = useState(0);
  const [perPage, setPerPage] = useState(10);

  const fetchMains = async page => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/transaction", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setTotalRows(response.data.total);
    setFrom(response.data.from);
    setLoading(false);
  };

  const handlePageChange = page => {
    fetchMains(page);
  };

  const handlePerRowsChange = async (newPerPage, page) => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/transaction", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setPerPage(newPerPage);
    setLoading(false);
  };

  useEffect(() => {
    fetchMains(1);
  }, []);

  const columns = [
    {
      name: '#',
      cell: (row, index) => index + from
    },
    {
      name: 'Tipe Pembayaran',
      selector: 'payment_type',
    },
    {
      name: 'Total Pembayaran',
      selector: 'paid_total',
    },
    {
      name: 'Opsi',
width : "20%",
      cell: row => {
        return <>
          <input
            type="month"
            className="p-2 my-2 border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
            onChange={(e) => handle(e)}
            id="month"
            value={data.month}
          />
          <a id="transaction-monthly" onClick={(e) => downloadReport(e)} className="bg-indigo-500 text-white active:bg-indigo-600 font-bold uppercase text-sm px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150">
            Download
          </a>
        </>
      },
    }
  ];

  function downloadReport(e) {
    e.preventDefault()
    const type = e.target.id.split("-")[0]
    const report_type = e.target.id.split("-")[1]

    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/report/export", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      },
      params: {
        from: data.dateFrom,
        to: data.dateTo,
        type: type,
        month: data.month,
        report_type: report_type,
        export_type: 'excel',
      }
    }).then(res => {
      window.location.href = res.data.data.url
    })
  }

  function handle(e) {
    const newData = { ...data }
    newData[e.target.id] = e.target.value
    setData(newData)
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">

          <div
            className={
              "relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white"
            }
          >
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <h3
                    className={
                      "font-semibold text-lg text-dark"
                    }
                  >
                    Daftar Transaksi
                  </h3>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <Link
                    className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/user-utama/transaction/add"
                  >
                    Tambah Transaksi
                  </Link>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">
              <DataTable
                columns={columns}
                data={main}
                progressPending={loading}
                pagination
                paginationServer
                paginationTotalRows={totalRows}
                onChangeRowsPerPage={handlePerRowsChange}
                onChangePage={handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
