import React, { useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
require("dotenv").config()

export default function Login() {
  const MySwal = withReactContent(Swal)
  const url = process.env.REACT_APP_URL_BACKEND + "/api/login"
  const [data, setData] = useState({
    email: localStorage.getItem("emailLogin") ? localStorage.getItem("emailLogin") : "",
    password: "",
    check: false
  })

  function submit(e) {
    e.preventDefault()

    Axios.post(url, {
      email: data.email,
      password: data.password
    }, {
      headers: {
        Accept: "application/json"
      }
    }).then(res => {

      if(res.data.status == false){
        return MySwal.fire(res.data.message)
      }

      localStorage.setItem("token", res.data.token)
      if (data.check === true) {
        localStorage.setItem("emailLogin", data.email)
      } else {
        localStorage.removeItem("emailLogin")
      }
      checkRole()
    });
  }

  function checkRole() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "loadedData": 1
      })
      
      if(res.data.message != "Unauthenticated."){
        if(res.data.data.level.id == 1){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/admin"
        }
        if(res.data.data.level.id == 2){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama"
        }
        if(res.data.data.level.id == 3){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-cabang"
        }
      }
    })
  }

  function handle(e) {
    const newData = { ...data }
    if (e.target.id === "check" && data.check === "on") {
      newData[e.target.id] = false
    } else {
      newData[e.target.id] = e.target.value
    }
    setData(newData)
  }

  return (
    <>
      <div className="container mx-auto px-4 h-full">
        <div className="flex content-center items-center justify-center h-full">
          <div className="w-full lg:w-4/12 px-4">
            <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
              <div className="rounded-t mb-0 px-3 py-3">

              </div>
              <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                <div className="text-dark text-center mb-3 font-bold">
                  <p>Sign In</p>
                  <hr className="mt-6 border-b-1 border-blueGray-300" />
                </div>
                <form onSubmit={(e) => submit(e)}>
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Email
                    </label>
                    <input
                      type="email"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Email"
                      onChange={(e) => handle(e)}
                      id="email"
                      value={data.email}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Password
                    </label>
                    <input
                      type="password"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Password"
                      onChange={(e) => handle(e)}
                      id="password"
                      value={data.password}
                    />
                  </div>
                  <div>
                    <label className="inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        className="form-checkbox border-0 rounded text-blueGray-700 ml-1 w-5 h-5 ease-linear transition-all duration-150"
                        id="check"
                        checked={data.check}
                        onChange={(e) => handle(e)}
                      />
                      <span className="ml-2 text-sm font-semibold text-blueGray-600">
                        Ingat saya
                      </span>
                    </label>
                  </div>

                  <div className="text-center mt-6">
                    <button
                      className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                      type="submit"
                    >
                      Sign In
                    </button>
                  </div>
                </form>
              </div>
            </div>
            <div className="flex flex-wrap mt-6 relative">
              <div className="w-1/2">
                <Link to="/auth/forgotPassword" className="text-blueGray-200">
                  <small>Lupa Password?</small>
                </Link>
              </div>
              <div className="w-1/2 text-right">
                <Link to="/auth/register" className="text-blueGray-200">
                  <small>Buat Akun Baru</small>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
