import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import DataTable from "react-data-table-component";
// components

export default function Index() {

  const [main, setMain] = useState([]);
  const [loading, setLoading] = useState(false);
  const [from, setFrom] = useState(false);
  const [totalRows, setTotalRows] = useState(0);
  const [perPage, setPerPage] = useState(10);

  const fetchMains = async page => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND+"/api/branch/stock-out", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setTotalRows(response.data.total);  
setFrom(response.data.from);   
    setLoading(false);
  };

  const handlePageChange = page => {
    fetchMains(page);
  };

  const handlePerRowsChange = async (newPerPage, page) => {
    setLoading(true);

    const response = await Axios.get(process.env.REACT_APP_URL_BACKEND+"/api/branch/stock-out", {
      params: {
        page: page,
        per_page: perPage,
        delay: 1
      },
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });

    setMain(response.data.data);
    setPerPage(newPerPage);
    setLoading(false);
  };

  useEffect(() => {
    fetchMains(1);
  }, []);

  const columns = [
    {
      name: '#',
      cell: (row, index) => index+from
    },
    {
      name: 'Produk',
      cell: row => row.product.name,
    },
    {
      name: 'Kuantitas',
      selector: 'qty',
    },
    {
      name: 'Keterangan Stok',
      cell: row => row.stock_type.name,
    },
  ];

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">

          <div
            className={
              "relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white"
            }
          >
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                  <h3
                    className={
                      "font-semibold text-lg text-dark"
                    }
                  >
                    Daftar Stok Keluar
                  </h3>
                </div>
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <Link
                    className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    to="/user-cabang/stock-out/add"
                  >
                    Tambah Stok Keluar
                  </Link>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">
              <DataTable
                columns={columns}
                data={main}
                progressPending={loading}
                pagination
                paginationServer
                paginationTotalRows={totalRows}
                onChangeRowsPerPage={handlePerRowsChange}
                onChangePage={handlePageChange}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
