import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

export default function Register() {
  const MySwal = withReactContent(Swal)
  const url = process.env.REACT_APP_URL_BACKEND + "/api/register"
  const [data, setData] = useState({
    branch_name: "",
    name: "",
    email: "",
    password: "",
    passwordConfirmation: "",
    check : false
  })

  function submit(e) {
    e.preventDefault()

    if(data.check === false){
      return MySwal.fire({
        title: <p>Setujui Kebijakan Privasi Kami</p>
      })
    }

    Axios.post(url, {
      branch_name: data.branch_name,
      name: data.name,
      email: data.email,
      password: data.password,
      password_confirmation: data.passwordConfirmation
    },{
      headers: {
        Accept: "application/json"
      }
    }).then(res => {
        MySwal.fire("Anda telah berhasil registrasi, mohon tunggu admin untuk verifikasi data anda")
        setTimeout(() => {
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/auth/login"
        }, 2000);
    }).catch((err) => {
      if (err.response.data.errors) {
        if (err.response.data.errors.branch_name) {
          return MySwal.fire(err.response.data.errors.branch_name[0])
        }
        if (err.response.data.errors.name) {
          return MySwal.fire(err.response.data.errors.name[0])
        }
        if (err.response.data.errors.email) {
          return MySwal.fire(err.response.data.errors.email[0])
        }
        if (err.response.data.errors.password) {
          return MySwal.fire(err.response.data.errors.password[0])
        }
      }
    });
  }

  function handle(e) {
    const newData = { ...data }
    if(e.target.id === "check" && data.check === "on"){
      newData[e.target.id] = false
    }else{
      newData[e.target.id] = e.target.value
    }
    setData(newData)
  }

  return (
    <>
      <div className="container mx-auto px-4 h-full">
        <div className="flex content-center items-center justify-center h-full">
          <div className="w-full lg:w-6/12 px-4">
            <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
              <div className="rounded-t mb-0 px-6 py-6">
                
              </div>
              <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                <div className="text-dark text-center mb-3 font-bold">
                  <p>Registrasi Pengguna</p>
                  <hr className="mt-6 border-b-1 border-blueGray-300" />
                </div>
                <form onSubmit={(e) => submit(e)}>
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Nama Cabang
                    </label>
                    <input
                      type="text"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Nama cabang"
                      onChange={(e) => handle(e)}
                      id="branch_name"
                      value={data.branch_name}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Nama
                    </label>
                    <input
                      type="text"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Nama"
                      onChange={(e) => handle(e)}
                      id="name"
                      value={data.name}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Email
                    </label>
                    <input
                      type="email"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Email"
                      onChange={(e) => handle(e)}
                      id="email"
                      value={data.email}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Password
                    </label>
                    <input
                      type="password"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Password"
                      onChange={(e) => handle(e)}
                      id="password"
                      value={data.password}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Konfirmasi Password
                    </label>
                    <input
                      type="password"
                      className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                      placeholder="Konfirmasi Password"
                      onChange={(e) => handle(e)}
                      id="passwordConfirmation"
                      value={data.passwordConfirmation}
                    />
                  </div>

                  <div>
                    <label className="inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        className="form-checkbox border-0 rounded text-blueGray-700 ml-1 w-5 h-5 ease-linear transition-all duration-150"
                        id="check"
                        checked={data.check}
                        onChange={(e) => handle(e)}
                      />
                      <span className="ml-2 text-sm font-semibold text-blueGray-600">
                        Saya setuju dengan{" "}
                        <a
                          href="#pablo"
                          className="text-lightBlue-500"
                          onClick={(e) => e.preventDefault()}
                        >
                          Kebijakan Privasi
                        </a>
                      </span>
                    </label>
                  </div>

                  <div className="text-center mt-6">
                    <button
                      className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                      type="submit"
                    >
                      Buat Akun
                    </button>
                  </div>
                </form>
              </div>
            </div>
            <div className="flex flex-wrap mt-6 relative">
              <div className="w-1/2">
              </div>
              <div className="w-1/2 text-right text-blueGray-200">
              Sudah Punya akun?&nbsp;
                <Link to="/auth/login" className="text-blueGray-200">
                  Login
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
