import Axios from "axios";
import React, { useEffect, useState } from "react";
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";

export default function Dashboard(props) {

    const [state, setState] = useState({
        labels: [
            "Tanggal 1",
            "Tanggal 2",
            "Tanggal 3",
            "Tanggal 4",
            "Tanggal 5",
            "Tanggal 6",
            "Tanggal 7",
            "Tanggal 8",
            "Tanggal 9",
            "Tanggal 10",
            "Tanggal 11",
            "Tanggal 12",
            "Tanggal 13",
            "Tanggal 14",
            "Tanggal 15",
            "Tanggal 16",
            "Tanggal 17",
            "Tanggal 18",
            "Tanggal 19",
            "Tanggal 10",
            "Tanggal 21",
            "Tanggal 22",
            "Tanggal 23",
            "Tanggal 24",
            "Tanggal 25",
            "Tanggal 26",
            "Tanggal 27",
            "Tanggal 28",
            "Tanggal 29",
            "Tanggal 30",
            "Tanggal 31",
        ],
        datasets: [{
            backgroundColor: 'rgba(75,192,192,1)',
            borderColor: 'rgba(0,0,0,1)',
            borderWidth: 2,
            data: []
        }]
    })

    const [data, setData] = useState({
        loadedData: 0,
        sku: 0,
        // Transaction
        transaction: 0,
        income_per_month: 0,
        // Non
        stok_in: 0,
        stok_out: 0,
        chartData: [],
        datasets: [
            {
                label: "Laporan Per-bulan",
                backgroundColor: "rgb(255, 99, 132)",
                borderColor: "rgb(255, 99, 132)",
                data: [0],
            },
        ],
    })

    useEffect(() => {
        if (data.loadedData == 0) {
            getData()
        }
    })

    async function getData() {
        let sku = 0
        let transaction = 0
        let income_per_month = 0
        let stok_in = 0
        let stok_out = 0
        let loadedData = 0

        await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/dashboard/chart-transaction", {
            headers: {
                Accept: "application/json",
                Authorization: `Bearer ${localStorage.getItem('token')}`
            }
        }).then(res => {
            setState({
                labels: [
                    "Tanggal 1",
                    "Tanggal 2",
                    "Tanggal 3",
                    "Tanggal 4",
                    "Tanggal 5",
                    "Tanggal 6",
                    "Tanggal 7",
                    "Tanggal 8",
                    "Tanggal 9",
                    "Tanggal 10",
                    "Tanggal 11",
                    "Tanggal 12",
                    "Tanggal 13",
                    "Tanggal 14",
                    "Tanggal 15",
                    "Tanggal 16",
                    "Tanggal 17",
                    "Tanggal 18",
                    "Tanggal 19",
                    "Tanggal 10",
                    "Tanggal 21",
                    "Tanggal 22",
                    "Tanggal 23",
                    "Tanggal 24",
                    "Tanggal 25",
                    "Tanggal 26",
                    "Tanggal 27",
                    "Tanggal 28",
                    "Tanggal 29",
                    "Tanggal 30",
                    "Tanggal 31",
                ],
                datasets: [{
                    label: "Laporan Per-bulan",
                    backgroundColor: 'rgba(75,192,192,1)',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: res.data.data
                }]
            })
        })

        await Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/branch/dashboard", {
            headers: {
                Accept: "application/json",
                Authorization: `Bearer ${localStorage.getItem('token')}`
            }
        }).then(res => {
            sku = res.data.data.sku ? res.data.data.sku : 0
            transaction = res.data.data.transaction ? res.data.data.transaction : 0
            income_per_month = res.data.data.income_per_month ? res.data.data.income_per_month : 0
            stok_in = res.data.data.stok_in ? res.data.data.stok_in : 0
            stok_out = res.data.data.stok_out ? res.data.data.stok_out : 0
            loadedData = 1
        })

        const newData = { ...data }
        newData['sku'] = sku
        newData['transaction'] = transaction
        newData['income_per_month'] = income_per_month
        newData['stok_in'] = stok_in
        newData['stok_out'] = stok_out
        newData['loadedData'] = loadedData
        setData(newData)
    }

    return (
        <>
            <div className="flex flex-wrap">

                <div className="w-full xl:w-8/12 mb-12 xl:mb-0 px-4">
                    <div className="relative flex flex-col min-w-0 break-words bg-transparent w-full mb-6 rounded">
                        <div className="rounded-t mb-0 px-4 py-3 bg-transparent">
                            <div className="flex flex-wrap items-center">
                                <div className="relative w-full max-w-full flex-grow flex-1 text-white mb-3">
                                    <h1 className="text-5xl font-semibold">Hallo, TIM!👋</h1>
                                    <p>Ini adalah halaman semua informasi yang Anda butuhkan</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                    <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                        <div className="rounded-t mb-0 px-4 py-3 bg-white">
                            <div className="flex flex-wrap items-center">
                                <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                    <h3 className="text-1xl font-semibold">Jumlah SKU</h3>
                                    <p className="text-2xl font-semibold">{data.sku}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {
                    props.cashier == 1 ?
                        <>

                            <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                                    <div className="rounded-t mb-0 px-4 py-3 bg-white">
                                        <div className="flex flex-wrap items-center">
                                            <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                                <h3 className="text-1xl font-semibold">Jumlah Transaksi</h3>
                                                <p className="text-2xl font-semibold">{data.transaction}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                                    <div className="rounded-t mb-0 px-4 py-3 bg-white">
                                        <div className="flex flex-wrap items-center">
                                            <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                                <h3 className="text-1xl font-semibold">Jumlah Pendapatan Perbulan</h3>
                                                <p className="text-2xl font-semibold">{data.income_per_month}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="w-full xl:w-12/12 mb-12 xl:mb-0 px-4 ">
                                <Line data={state} />
                            </div>
                        </> :
                        <>
                            <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                                    <div className="rounded-t mb-0 px-4 py-3 bg-white">
                                        <div className="flex flex-wrap items-center">
                                            <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                                <h3 className="text-1xl font-semibold">Jumlah Stok Masuk</h3>
                                                <p className="text-2xl font-semibold">{data.stok_in}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="w-full xl:w-6/12 mb-12 xl:mb-0 px-4 ">
                                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 rounded">
                                    <div className="rounded-t mb-0 px-4 py-3 bg-white">
                                        <div className="flex flex-wrap items-center">
                                            <div className="relative w-full max-w-full flex-grow flex-1 text-blueGray-700 mb-3">
                                                <h3 className="text-1xl font-semibold">Jumlah Stok Keluar</h3>
                                                <p className="text-2xl font-semibold">{data.stok_out}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </>
                }



            </div>
        </>
    );
}
