import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

export default function Edit() {
  const MySwal = withReactContent(Swal)

  const [data, setData] = useState({
    loadedData: 0,
    name : "",
    branch_name : "",
    email : "",
    password : "",
    old_password : "",
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        name: res.data.data.name,
        branch_name: res.data.data.branch_name,
        email: res.data.data.email,
      })
    })
  }

  function submit(e) {
    e.preventDefault()

    // MySwal.fire({
    //   title: "Yakin akan merubah data?",
    //   icon: "info",
    //   showCloseButton: true,
    //   showCancelButton: true,
    //   focusConfirm: false,
    //   confirmButtonText: 'Yakin',
    //   cancelButtonText: 'Batal'

    // }).then((val) => {
      const id = window.location.href.split("/")[6]
      Axios.put(process.env.REACT_APP_URL_BACKEND + "/api/info/" + id, {
        name: data.name,
        branch_name: data.branch_name,
        email: data.email,
        old_password: data.old_password,
        password: data.password,
      }, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      }).then(res => {
        getData()
        MySwal.fire({
          title: 'Berhasil',
          text: 'Data berhasil dirubah',
          icon: 'success'
        });
        window.location.reload()
      }).catch((err) => {
        MySwal.fire({
          title: 'Gagal',
          text: err.response.data.message,
          icon: 'error'
        });
      });
    // })
  }

  function handle(e) {
    const newData = { ...data }
    newData[e.target.id] = e.target.value
    setData(newData)
  }

  return (
    <>
      <div className="flex flex-wrap mt-4">
        <div className="w-full mb-12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded text-dark bg-white">
            <div className="rounded-t mb-0 px-4 py-3 border-0">
              <div className="flex flex-wrap items-center">
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <h3 className="font-semibold text-lg text-dark">
                    Profile
                  </h3>
                </div>
              </div>
            </div>
            <div className="block w-full overflow-x-auto">

              <form onSubmit={(e) => submit(e)} className="p-5">
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama"
                    onChange={(e) => handle(e)}
                    id="name"
                    value={data.name}
                  />
                </div>
    
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Nama Branch
                  </label>
                  <input
                    type="text"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Nama Branch"
                    onChange={(e) => handle(e)}
                    id="branch_name"
                    value={data.branch_name}
                  />
                </div>
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Email"
                    onChange={(e) => handle(e)}
                    id="email"
                    value={data.email}
                  />
                </div>
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Password Lama
                  </label>
                  <input
                    type="password"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Password Lama"
                    onChange={(e) => handle(e)}
                    id="old_password"
                    value={data.old_password}
                  />
                </div>
                <div className="w-full mb-3">
                  <label
                    className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Password Baru
                  </label>
                  <input
                    type="password"
                    className="border-0 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none w-full ease-linear transition-all duration-150"
                    placeholder="Password Baru"
                    onChange={(e) => handle(e)}
                    id="password"
                    value={data.password}
                  />
                </div>

                <div className="text-center mt-6">
                  <button
                    className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                    type="submit"
                  >
                    Simpan
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
