import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

// components

import Navbar from "components/Navbars/AuthNavbar.js";
import FooterSmall from "components/Footers/FooterSmall.js";

// views

import Login from "views/auth/Login.js";
import Register from "views/auth/Register.js";
import ForgotPassword from "views/auth/ForgotPassword.js";
import ResetPassword from "views/auth/ResetPassword.js";
import Axios from "axios";

export default function Auth() {
  const [data, setData] = useState({
    loadedData: 0
  })

  useEffect(() => {
    if (data.loadedData == 0) {
      getData()
    }
  })

  function getData() {
    Axios.get(process.env.REACT_APP_URL_BACKEND + "/api/info", {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(res => {
      setData({
        "loadedData": 1
      })
      
      if(res.data.message != "Unauthenticated."){
        if(res.data.data.level.id == 1){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/admin"
        }
        if(res.data.data.level.id == 2){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-utama"
        }
        if(res.data.data.level.id == 3){
          window.location.href = process.env.REACT_APP_URL_FRONTEND + "/user-cabang"
        }
      }
    })
  }
  
  return (
    <>
      <Navbar transparent />
      <main>
        <section className="relative w-full h-full py-40 min-h-screen">
          <div
            className="absolute top-0 w-full h-full bg-blueGray-800 bg-no-repeat bg-full"
            style={{
              backgroundImage:
                "url(" + require("assets/img/register_bg_2.png").default + ")",
            }}
          ></div>
          <Switch>
            <Route path="/auth/login" exact component={Login} />
            <Route path="/auth/register" exact component={Register} />
            <Route path="/auth/ForgotPassword" exact component={ForgotPassword} />
            <Route path="/auth/reset-password" exact component={ResetPassword} />
            <Redirect from="/auth" to="/auth/login" />
          </Switch>
          <FooterSmall absolute />
        </section>
      </main>
    </>
  );
}
