import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

export default function ForgotPassword() {
    const MySwal = withReactContent(Swal)

    const [data, setData] = useState({
        password: "",
        password_confirmation: "",
    })

    function submit(e) {
        e.preventDefault()

        MySwal.fire({
            title: "Yakin akan memulihkan password?",
            icon: "info",
            showCloseButton: true,
            showCancelButton: true,
            focusConfirm: false,
            confirmButtonText: 'Yakin',
            cancelButtonText: 'Batal'

        }).then((val) => {
            const token = window.location.pathname.split('/')[2]
            Axios.post(process.env.REACT_APP_URL_BACKEND + "/api/reset-passwrd", {
                token: token,
                password: data.password,
                password_confirmation: data.password_confirmation,
            }, {
                headers: {
                    Accept: "application/json",
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                }
            }).then(res => {
                MySwal.fire({
                    title: 'Berhasil',
                    text: 'Pemulihan password berhasil, Silahkan Login ',
                    icon: 'success'
                });
                window.location.href = process.env.REACT_APP_URL_FRONTEND + "/auth/login"

            }).catch((err) => {
                MySwal.fire({
                    title: 'Gagal',
                    text: err.response.data.message,
                    icon: 'error'
                });
            });
        })
    }

    function handle(e) {
        const newData = { ...data }
        newData[e.target.id] = e.target.value
        setData(newData)
    }


    return (
        <>
            <div className="container mx-auto px-4 h-full">
                <div className="flex content-center items-center justify-center h-full">
                    <div className="w-full lg:w-6/12 px-4">
                        <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
                            <div className="rounded-t mb-0 px-6 py-6">

                            </div>
                            <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                                <div className="text-dark text-center mb-3 font-bold">
                                    <p>Pemulihan Password</p>
                                    <hr className="mt-6 border-b-1 border-blueGray-300" />
                                </div>
                                <form onSubmit={(e) => submit(e)}>
                                    <div className="relative w-full mb-3">
                                        <small>Masukkan Password untuk pemulihan akun anda. </small>
                                        <br /><br />
                                        <label
                                            className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                                            htmlFor="grid-password"
                                        >
                                            Passowrd
                                        </label>
                                        <input
                                            type="password"
                                            className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                                            placeholder="Password"
                                            onChange={(e) => handle(e)}
                                            id="password"
                                            value={data.password}
                                        />
                                    </div>
                                    <div className="relative w-full mb-3">
                                        <label
                                            className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                                            htmlFor="grid-password"
                                        >
                                            Konfirmasi Passowrd
                                        </label>
                                        <input
                                            type="password"
                                            className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                                            placeholder="Konfirmasi Password"
                                            onChange={(e) => handle(e)}
                                            id="password_confirmation"
                                            value={data.password_confirmation}
                                        />
                                    </div>

                                    <div className="text-center mt-6">
                                        <button
                                            className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                                            type="submit"
                                        >
                                            Pulihkan
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div className="flex flex-wrap mt-6 relative">
                            <div className="w-1/2">
                            </div>
                            <div className="w-1/2 text-right text-blueGray-200">
                                <Link to="/auth/login" className="text-blueGray-200">
                                    Login Disini
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
